.project-card-container {
  width: 60vw;
  height: auto;
  display: flex;
  flex-direction: column;
}

.project-card {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 50px;
}

.project-card-image {
  margin: 0 auto;
  width: 200px;
  height: 200px;
  border-radius: 100%;
}

.project-card-tech,
.project-card-credits {
  font-size: 1.2rem;
  text-align: center;
  color: #5ef275;
  font-weight: 900;
  opacity: 0.5;
}

.btn-margin {
  margin-right: 20px;
}

.btn-link {
  color: #fff;
  text-decoration: none;
}

.btn-link:hover {
  color: #fff;
}

.project-card-button:hover {
  background-color: #5ef275;
  transition: 0.5s;
  transform: scale(1.1);
  font-weight: bold;
  border: none;
}

.project-card-button:not(:hover) {
  background: #333;
  transition: 0.7s;
  transform: scale(1);
}

.project-card-button {
  align-self: center;
  font-size: 25px;
  background: #333;
  border: none;
  padding: 15px;
  border-radius: 5px;
  width: 180px;
}

.project-card-description {
  text-align: center;
  font-size: 1.5rem;
  padding: 15px;
  color: rgb(255, 255, 255);
}

.project-card-title {
  padding: 20px;
  text-align: center;
  font-size: 2.5rem;
}

@media screen and (max-width: 768px) {
  .project-card-conatiner {
    width: 50vw;
    height: auto;
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  .project-card-image {
    width: 40vw;
    height: 40vw;
    border-radius: 100%;
    margin-bottom: 20px;
  }

  .project-card-tech {
    align-items: center;
    font-size: 1.2rem;
  }

  .project-card-title {
    font-size: 1.8rem;
  }

  .project-card-none {
    display: none;
  }

  .project-card-button:hover {
    background-color: #5ef275;
    transform: scale(1.0);
    font-weight: bold;
    border: none;
    color: #2F3A59;

  }

  .project-card-button:not(:hover) {
    background: #5ef275;
    transform: scale(1);
    color: #2F3A59;

  }

  .project-card-button {
    align-self: center;
    font-size: 15px;
    background: #5ef275;
    border: none;
    padding: 15px;
    border-radius: 5px;
    width: 100px;
  }

 .project-card {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 50px;
  }
}
